/* individual workout page */
.wo-ex-picture {
    height: 100px;
    border-radius: 10px;
    border: 2px solid #0C2340;
}
.img-div {
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 35px;
    color: #008BEE;
}
.wo-div {
    margin-top: 4em;
}
.ex-information {
    font-size: 15px;
}
.ex-list-row {
    width: 100%;
}
.wo-general {
    background-color: #236192;
    padding: 2em;
    color: white;
    border-radius: 10px;
    margin-bottom: 3em;
}
.ex-one {
    width: 100%;
    padding: 1em ;
    border-top: 2px solid #CFCFCF;
}
.wo-reps-sets {
    display: flex;
    justify-content: center;
    align-items: center;
}
.sets {
    margin-right: 2em;
}
.wo-ex-name {
    display: flex;
    align-items: center;
    justify-content: center;
}
.ind-wo-author {
    padding-top: 50px;
}
.ind-wo-ex-img {
    width: 75%;
    height: 130px;
}
.ind-wo-ex-name {
    color: #008BEE;
}
.ind-wo-ex-name:hover,
.ind-wo-ex-name:focus {
    color: black;
    text-decoration: none;
}
.ind-wo-ex-rs {
    font-size: 20px;
    margin: 0 auto;
}
.wo-ex-info {
    margin: auto;
    text-align: center;
}
.ind-wo-edit {
    margin-top: 50px;
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 30px;
}
.num-div {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
}
.ind-wo-about {
    color: #008BEE;
    border-bottom: 2px solid #CFCFCF;
    padding: 0 0 5px 0;
}


@media (max-width: 360px) {
    .bc-active {
        font-size: 12px;
    }
    .bc-before {
        margin-left: 10px;
        font-size: 12px;
    }
    .bread-crumb { 
        height: 60px;
    }
    .ind-ex-h {
        font-size: 2rem;
    }
    .ex-pic {
        width: 100%;
    }
    .ind-wo-about {
        font-size: 1.4rem;
    }
    p {
        font-size: 16px;
    }
    .new-wo-h {
        padding-top: 20px ;
        margin-left: 10%;
        width: 80%;
    }
}

@media (min-width: 360px) {
    .bc-active {
        font-size: 14px;
    }
    .bc-before {
        margin-left: 10px;
        font-size: 14px;
    }
    .bread-crumb { 
        height: 60px;
    }
    .ex-pic {
        width: 100%;
    }
    .new-wo-h {
        padding-top: 20px ;
        margin-left: 10%;
        width: 80%;
    }
}
@media (min-width: 421px) {
    .bc-active {
        font-size: 18px;
    }
    .bc-before {
        margin-left: 20px;
        font-size: 18px;
    }
    .bread-crumb { 
        height: 80px;
    }
    .all-wo-row {
        padding-top: 20px;
    }
    .ex-pic {
        width: 60%;
    }
}
